@import url(https://fonts.googleapis.com/css2?family=Space+Mono&display=swap);
* {
  font-family: "Space Mono", monospace;
  color: white;
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(40deg, rgb(10, 10, 35), rgb(20, 20, 65));
  overflow: hidden;
}

.row {
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
}

.centered {
  justify-content: center;
  align-items: center;
}

.shadow {
  box-shadow: 0px 0px 10px 0.5px black;
}

.app {
  height: 100vh;
  justify-content: flex-start;
}

.scene {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
}

.animated {
  -webkit-animation: append 0.5s linear 1;
          animation: append 0.5s linear 1;
}

*::-webkit-scrollbar {
  width: 5px;
}

*::-webkit-scrollbar-track {
  background: rgb(30, 30, 50);
}

*::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, aquamarine, grey 30%);
  border-radius: 10px;
}

@-webkit-keyframes append {
  0% {
    opacity: 0;
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes append {
  0% {
    opacity: 0;
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}


